import React from 'react';
import {Table} from "react-bootstrap";
import {LoadingIcon} from "../components/loading";

class About extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      users: [],
      loading:true,
    }
  }

  componentDidMount(){
    if (!localStorage.getItem("api_token"))
    return;
    fetch("https://jsonplaceholder.typicode.com/users", {
      method: "GET"
    })
    .then( r => {
      this.setState({loading:false})
      if (r.ok) {
        return r.json()
      }
    })
    .then( r => this.setState({users: r}))
    .catch( r => {
      console.log(r);
    });
  }

  render() {
    const users = this.state.users.map((r) => (
      <tr key={r.id}>
        <td>{r.id}</td>
        <td>{r.name}</td>
        <td>{r.email}</td>
      </tr>
    ));
    if (!this.state.loading){
      return (
        <div>
          <Table key={"table-key"} striped bordered condensed hover>
            <thead>
              <tr>
                <th>#</th>
                <th>First Name</th>
                <th>E-mail</th>
              </tr>
            </thead>
            <tbody>
              {users}
            </tbody>
          </Table>
        </div>
      );
    }
    else {
      return <LoadingIcon/>;
    }

  }
}

export default About;



