import React, { Component } from 'react';
import { Button, Modal, FormControl } from 'react-bootstrap';
import { Redirect } from 'react-router-dom'

export default class LoginModal extends Component {

  constructor(props){
    super(props);
    this.state = {
      username: '',
      password: '',
      logIn: false,
    }
  }
  onClickLogIn(){
    this.props.logUserIn()
    this.setState({
      logIn:true,
    })
  }

  render(){
    if (this.state.logIn) {
      return  <Redirect to="/"/>
    }
    return (
      <Modal
        show={true}
        >
        <Modal.Header>
          <h4>Login</h4>
        </Modal.Header>
        <Modal.Body>
          <FormControl
            type="text"
            value={this.state.username}
            bsSize="sm"
            placeholder="username"
            onChange={(e) => this.setState({username:e.target.value})}
            />
          <br/>
          <FormControl
            type="text"
            bsSize="sm"
            value={this.state.password}
            onChange={(e) => this.setState({password:e.target.value})}
            placeholder="password"
            />
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={() => this.onClickLogIn()} bsSize="sm" bsStyle="primary" className="pull-right">log in</Button>
        </Modal.Footer>
      </Modal>
    );
  }

}



