import React, { Component } from 'react';
import { ListGroup, ListGroupItem, Button, Col } from 'react-bootstrap';
import TodoForm from '../components/form';
import { CSSTransitionGroup } from 'react-transition-group'
import "./todolist.css";

function RenderItemList(props){
  return (
    <ListGroupItem>
      <div className="row">
        <div className="col-md-10">
          {props.item}
        </div>
        <div className="col-md-2 text-right">
          <Button onClick={() => props.removeItem(props.item)} bsStyle="warning" bsSize="xs"><small>DONE</small></Button>
        </div>
      </div>
    </ListGroupItem>
  );
}

export default class TodoList extends Component {

  constructor(props){
    super(props);
    this.state = {
      items: [
        'Item 1',
        'Item 2',
        'Item 3'
      ],
    }
  }

  addToItems(a){
    const items = this.state.items.slice(0);
    if (items.indexOf(a) !== -1){
      alert("Already sent")
      return false;
    }

    items.unshift(a);
    this.setState({items})
    return true;
  }

  removeItem(a){
    this.setState({
      items: this.state.items.filter( e => e!==a)
    })
  }
  render(){
    const items = this.state.items.map((r) => (
      <RenderItemList key={r} item={r} removeItem={this.removeItem.bind(this)} />
    )
    );
    return (
      <Col md={12}>
        <div className="text-center">
          <TodoForm addToItems={this.addToItems.bind(this)}></TodoForm>
        </div>
        <hr/>

        <ListGroup>
          <CSSTransitionGroup
            transitionName="example"
            transitionEnterTimeout={500}
            transitionLeaveTimeout={300}>
            {items}
          </CSSTransitionGroup>

        </ListGroup>

      </Col>
    );
  }

}



