import React, { Component } from 'react';
import { PageHeader, Jumbotron, Col, Row, Nav, NavItem, Grid} from 'react-bootstrap';
import TodoList from './screens/todolist';
import About from "./screens/about";
import { Route, Redirect, withRouter} from 'react-router-dom'
import {LinkContainer} from 'react-router-bootstrap';
import "./App.css";
import LoginModal from "./components/login";
import FontAwesome from "react-fontawesome";

const Home = () => (
  <div>
    <h1>Home!</h1>
  </div>
)

class App extends Component {

  constructor(props){
    super(props);
    let token = localStorage.getItem("api_token");
    this.state = {
      logged: token ? true : false, // it should be set on redux to avoid user to enter /login on browser
    }
  }

  logUserIn(){
    localStorage.setItem("api_token", "haha");
    this.setState({
      logged:true,
    });
  }
  componentWillMount(){
    fetch('http://carroetecnica.com.br/wp-json/wp/v2/posts/', {
      method: 'GET'
    })
    .then(r => {
      if (r.ok){
        return r.json();
      }
      else {
        alert("Não foi possível carregar os posts");
      }
    })
    .then(r => {
      console.log(r);
    })
  }

  logUserOut(){
    localStorage.removeItem("api_token");
    this.setState({
      logged:false,
    });
  }

  render() {
    return (
      <div>
        <div className="text-center">
          <Jumbotron>
            <PageHeader>
              <p>
                TESTDRIVING APP
              </p>
              <p>
                <FontAwesome name='rocket' />
              </p>
              <p>
                <small>just to test stuff around :)</small>
              </p>
            </PageHeader>
          </Jumbotron>
        </div>
        <Grid>
          <Row>
            <Col md={3}>
              <Nav bsStyle="pills" stacked>
                <LinkContainer exact href="/" to="/">
                  <NavItem>Home</NavItem>
                </LinkContainer>
                <LinkContainer href="/about" to="/about">
                  <NavItem>API List app</NavItem>
                </LinkContainer>
                <LinkContainer href="/todo" to="/todo">
                  <NavItem>TODO App</NavItem>
                </LinkContainer>
                <NavItem onClick={() => this.logUserOut()}>Logout</NavItem>
              </Nav>
            </Col>
            <Col md={9}>
              <Route key={0} exact path="/" component={Home}/>
              <Route key={1} path="/about" component={About}/>
              <Route key={2} path="/todo" component={TodoList}/>
              <Route key={4} path="/login" render={(props) => (
                  <LoginModal {...props} logUserIn={this.logUserIn.bind(this)}/>
                )}/>
              {!this.state.logged && this.props.location.pathname!=="/login" && <Redirect to="/login"/>}
              </Col>
            </Row>
          </Grid>
        </div>
      );
    }
  }

  export default withRouter(App);



